import {useState, useEffect} from 'react';
import { selectUser } from '../../redux/userReducer';
import { selectCurOrders, selectCurCount, addCurOrders, cleanCurOrders } from '../../redux/orderReducer';
import { useSelector, useDispatch } from 'react-redux';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faClipboard, faFloppyDisk, faPercent, faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';
import { selectProducts, getProducts } from '../../redux/productReducer';
import { floatAdd, floatMul } from '../../utils/utils';

const Sidebar = () => {
  const dispatch = useDispatch();
  const user = useSelector(selectUser);
  const orders = useSelector(selectCurOrders);
  const orderCount = useSelector(selectCurCount);
  const products = useSelector(selectProducts);
  const [totalCost, setTotalCost] = useState(0);

  useEffect(() => {
    dispatch(getProducts());
  }, []);

  useEffect(() => {
    let cost = 0;
    orders.map((o, index) => {
        const p = getProduct(o.productId);
        cost = floatAdd(cost, floatMul(p.price, o.amount));
    });
    setTotalCost(cost);
    
  }, [orderCount])

  const getProduct = (id) => {
    const p = products.filter((e) => e._id === id);
    return p[0];
  }

  const handleMinus = (p) => {
    const order = {
        productId: p._id,
        amount: -1
    }
    dispatch(addCurOrders(order));
  }

  const handlePlus = (p) => {
    const order = {
        productId: p._id,
        amount: 1
    }
    dispatch(addCurOrders(order));
  }

  return (
    <div className='bg-[#D9D9D9] h-[calc(100vh-62px)] w-[0px] sm:w-[480px] md:w-[480px] lg:w-[480px] xl:w-[480px] transition-all'>
        <div className='w-full h-full relative'>
            <div className='w-full h-[50vh] overflow-x-auto px-4'>
                {
                    orders.map((o, index) => {
                        const p = getProduct(o.productId);
                        return (
                            <div key={index} className="border-b border-gray-700 py-4">    
                                <div className='flex justify-between text-md'>
                                    <div>{p.name}</div>
                                    <div>{p.price}</div>
                                </div>
                                <div className='flex justify-between text-sm'>
                                    <div>SKU: 12345617872</div>
                                    <div className='flex justify-center items-center'>
                                        <FontAwesomeIcon 
                                            icon={faMinus}
                                            className="cursor-pointer"
                                            onClick={()=>handleMinus(p)}
                                        />
                                        <p className='px-2 select-none'>{o.amount}</p>
                                        <FontAwesomeIcon 
                                            icon={faPlus} 
                                            className="cursor-pointer"
                                            onClick={()=>handlePlus(p)}
                                        />
                                    </div>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            <div className='w-full absolute bottom-0 px-6 py-2'>
                <div className='flex justify-between'>
                    <p>Subtotal</p>
                    <p>{floatAdd(totalCost, floatMul(totalCost, -0.2))}</p>
                </div>
                <div className='flex justify-between'>
                    <p>VAT (@20%)</p>
                    <p>{floatMul(totalCost, 0.2)}</p>
                </div>
                <div className='flex justify-between font-bold'>
                    <p>Total</p>
                    <p>{totalCost}</p>
                </div>
                <div className='flex justify-between mt-2'>
                    <div className='flex justify-between gap-2'>                        
                        <button 
                            className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}            
                        ><FontAwesomeIcon icon={faClipboard}/></button>
                        <button 
                            className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}            
                        ><FontAwesomeIcon icon={faFloppyDisk}/></button>
                        <button 
                            className={`w-[2rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}            
                        ><FontAwesomeIcon icon={faPercent}/></button>
                    </div>
                    <button 
                        className={`w-[6rem] h-[2rem] text-md rounded-[6px] leading-[2rem] bg-[#404040] text-gray-100`}            
                    >Checkout</button>
                </div>
            </div>
        </div>
    </div>
  );
}

export default Sidebar;