import {useState} from 'react';
import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import AddCategoryModal from './add.category.modal';
import AddProductModal from './add.product.modal';
import { selectCategories, getCategories } from '../../../redux/pcategoryReducer';
import { selectProducts, getProducts } from '../../../redux/productReducer';
import { useSelector, useDispatch } from 'react-redux';
import { useEffect } from 'react';

const Inventory = () => {
  const dispatch = useDispatch();
  const [categoryModal, setCategoryModal] = useState(false);
  const [productModal, setProductModal] = useState(false);
  const categories = useSelector(selectCategories);
  const products = useSelector(selectProducts);

  useEffect(() => {
    dispatch(getCategories());
    dispatch(getProducts());
  }, [])

  const categoryName = (id) => {
    const cate = categories.filter((c) => c._id === id);
    return cate[0].name;
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-62px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Inventory</p>
              </div>
              <div className='w-[50%] flex px-[2rem] mt-[2rem] text-gray-100 text-xl italic'>
                <button 
                    className={`w-[10rem] text-md leading-[2.1rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800`}
                    onClick={() => setCategoryModal(true)}
                >Add Category</button>
                <button 
                    className={`w-[10rem] text-md leading-[2.1rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 ml-6`}
                    onClick={() => setProductModal(true)}
                >Add Product</button>
              </div>
              <div className='text-md divide-y divide-gray-400 py-1 mt-2 px-[2rem]'>
                <div className='text-xl font-bold uppercase text-gray-100 mb-2'>Categories</div>
                <div className='h-[28vh] overflow-y-auto'>
                  {
                    categories.map((c, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem]'>
                          <p className='w-[70%]'>{c.name}</p>
                          <p className='w-[15%] text-center select-none cursor-pointer'>Edit</p>
                          <p className='w-[15%] text-center select-none cursor-pointer'>Delete</p>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
              <div className='text-md divide-y divide-gray-400 py-1 mt-2 px-[2rem]'>
                <div className='text-xl font-bold uppercase text-gray-100 mb-2'>PRODUCTS</div>
                <div className='h-[28vh] overflow-y-auto'>
                  {
                    products.map((p, index) => {
                      return (
                        <div key={index} className='w-full flex text-gray-100 leading-[1.4rem]'>
                          <p className='w-[70%]'>{p.name}{' > '}{categoryName(p.categoryId)}</p>
                          <p className='w-[15%] text-center select-none cursor-pointer'>Edit</p>
                          <p className='w-[15%] text-center select-none cursor-pointer'>Delete</p>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
            </div>
            <Sidebar />
        </div>
        <AddCategoryModal isModal={categoryModal} setIsModal={setCategoryModal} />
        <AddProductModal isModal={productModal} setIsModal={setProductModal} categories={categories} />
    </Layout>
  );
}

export default Inventory;