import { Menu, Transition } from "@headlessui/react";
import { Fragment } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretLeft, faUser } from '@fortawesome/free-solid-svg-icons'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { useState } from 'react';
import { selectUser, updateTooltip } from '../../redux/userReducer';
import { API_URL } from '../../config/constants';
import ReactTooltip from "react-tooltip";
import Tooltip from 'react-power-tooltip';
import { getNotification, selectNotifications, updateNotification, updateNotificationShow } from '../../redux/notificationReducer';

export const Navbar = ({
  customSettingComponent,
  searchBar = true,
  searchKeyword = '',
  normalBar = false,
  disableRealestate = false
}) => {

  const navigate = useNavigate();

  const onLogout = (e) => {
    navigate('/logout');
  }
  const [isMore, setIsMore] = useState(false);
  const user = useSelector(selectUser);
  const dispatch = useDispatch();
  const [keyword, setKeyword] = useState(searchKeyword);
  const notifications = useSelector(selectNotifications);

  const onEditProfile = (e) => {
    navigate('/user/edit');
  }

  const handleBack = () => {
    navigate(-1);
  }

  return (
    <div className="navbar w-full">
      <div className="flex items-center w-full justify-between">
        {/* {user && user._id && */}
        <>
          <Menu as="div" className="relative inline-block text-left nav-menu mr-5 mt-1">
            <Menu.Button onClick={() => handleBack()} data-tip data-for="notificationId" className="inline-flex w-full justify-center rounded-md text-sm text-gray-400 hover:text-gray-100">
              <FontAwesomeIcon icon={faCaretLeft} className="text-3xl"/>
            </Menu.Button>
          </Menu>

          {searchBar && (
            <>
              <input 
                    className="appearance-none italic bg-transparent border-b border-gray-300 w-[30%] text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                    type="search"
                    placeholder='Search Product' />
              <input 
                    className="appearance-none italic bg-transparent border-b border-gray-300 w-[30%] text-gray-100 mr-3 py-1 px-2 leading-[2rem] focus:outline-none" 
                    type="search"
                    placeholder='Search Customer' />
            </>
          )}
          
          <Menu as="div" className="relative inline-block text-left nav-menu mt-1">
            <Menu.Button className="inline-flex w-full justify-center rounded-md text-sm text-gray-400 hover:text-app-black">
              <FontAwesomeIcon icon={faUser} className="text-4xl"/>
            </Menu.Button>

            <Transition
              as={Fragment}
              enter="transition ease-out duration-100"
              enterFrom="transform opacity-0 scale-95"
              enterTo="transform opacity-100 scale-100"
              leave="transition ease-in duration-75"
              leaveFrom="transform opacity-100 scale-100"
              leaveTo="transform opacity-0 scale-95"
            >
              <Menu.Items className="absolute right-0 mt-2 w-56 origin-top-right divide-y divide-gray-100 rounded-sm bg-white shadow-lg ring-2 ring-gray-200 focus:outline-none z-10">
                <div className="px-1 py-1 ">
                  {/* <Menu.Item>
                    {({ active }) => (
                      <button
                        className={`${
                          active ? "bg-red-500 text-white" : "text-gray-900"
                        } group  w-full items-center rounded-sm px-2 py-2 text-sm text-center`}
                        onClick={(e) => onEditProfile(e)}
                      >
                        Edit Profile
                        &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                      </button>
                    )}
                  </Menu.Item> */}
                  <Menu.Item>
                    {({ active }) => (
                      <button
                        className={`${
                          active ? "bg-[#404040] text-white" : "text-gray-900"
                        } group  w-full items-center rounded-sm px-2 py-2 text-sm text-center`}
                        onClick={(e) => onLogout(e)}
                      >
                        Logout
                        &nbsp;<i className="fa fa-right-from-backet">&nbsp;</i>
                      </button>
                    )}
                  </Menu.Item>
                </div>
              </Menu.Items>
            </Transition>
          </Menu>
        </>
        {/* } */}
      </div>
    </div>
  );
};